<template>
  <div class="main">
    <div>
      <!-- 二级路由插座 -->
      <router-view></router-view>
    </div>

    <van-tabbar v-model="tabBarName" @change="toggleTabBar">
      <van-tabbar-item
        v-for="(item, index) in tabBarData"
        :key="index"
        :icon="item.icon"
      >{{item.title}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabBarName: 0,

      //底部导航数据
      tabBarData: [
        {
          icon: "wap-home",
          title: "首页",
          routeName: "Home",
          url: "/main/home"
        },
        {
          icon: "qr",
          title: "分类",
          routeName: "Category",
          url: "/main/category"
        },
        {
          icon: "shopping-cart-o",
          title: "购物车",
          routeName: "Shopcart",
          url: "/main/shopcart"
        },
        {
          icon: "contact",
          title: "我",
          routeName: "My",
          url: "/main/my"
        }
      ]
    };
  },
  created() {
    //获取路径的hash
    let hash = location.hash.slice(1);
    //根据路径激活相应路由
    for (let i = 0; i < this.tabBarData.length; i++) {
      if (this.tabBarData[i].url == hash) {
        this.tabBarName = i;
        break;
      }
    }
  },
  updated(){
        //获取路径的hash
    let hash = location.hash.slice(1);
    //根据路径激活相应路由
    for (let i = 0; i < this.tabBarData.length; i++) {
      if (this.tabBarData[i].url == hash) {
        this.tabBarName = i;
        break;
      }
    }
  },
  methods: {
    //切换底部导航
    toggleTabBar(index) {
      this.$router.push({ name: this.tabBarData[index].routeName });
    }
  },

};
</script>

<style lang="less" scoped>
.van-tabbar {
  border-top: 1px solid rgb(170, 170, 167);
  background-color: rgb(243, 243, 239);
  .van-tabbar-item--active {
    color: rgb(50, 150, 248);
  }
}
</style>